.container {
  position: absolute;
  bottom: 13px;
  right: 13px;
  .deleteButton{
    cursor: pointer;
    background-color: #FFFFFF;
    //border: 1px #DFDFDF solid;
    border: 0;
    border-radius: 6px;
    width: 110px;
    height: 29px;


    &:active{
      transform: translateY(1px);
    }

  }
}

@media (max-width: 820px) {

  .container {
    .deleteButton {

      cursor: default;
    }

  }
}


